.Entete {
    position: sticky;
    z-index: 10;
    top: 0;
    height: 60px;
    margin: 0;
    background-color: #2e2e2e;
    display: flex;
    color: white;
    justify-content: space-between;
    padding-right: 10px;
    padding-left: 10px;
    ul {
        list-style: none;
        padding: 0;
        margin: 10px;
        padding-top: 5px;
        display: flex;
        flex-direction: row;
        li {
            margin-left: 10px;
        }
        
    }
}